'use client';

import React, { useState } from 'react';
import { usePathname } from 'next/navigation';
import Link from 'next/link';
import { Menu, X } from 'lucide-react';

const NAV_LINKS = [
  { label: 'Dashboard',   href: '/' },
  { label: 'Portfolios',  href: '/portfolios' },
  { label: 'Performance', href: '/performance' },
  { label: 'News',        href: '/news' },
  { label: 'About',       href: '/about' },
];

export default function Navbar() {
  const pathname = usePathname();
  const [mobileOpen, setMobileOpen] = useState(false);

  return (
    <>
      <nav style={{
        position: 'fixed',
        top: 0, left: 0, right: 0,
        height: '56px',
        zIndex: 50,
        background: 'rgba(10, 12, 16, 0.92)',
        backdropFilter: 'blur(12px)',
        borderBottom: '1px solid var(--border)',
      }}>
        <div className="container" style={{ height: '100%', display: 'flex', alignItems: 'center', gap: '32px' }}>

          {/* Wordmark */}
          <Link href="/" style={{ textDecoration: 'none', flexShrink: 0, display: 'flex', alignItems: 'center', gap: '10px' }}>
            {/* Minimal shield mark */}
            <svg width="22" height="24" viewBox="0 0 22 24" fill="none">
              <path d="M11 0L0 4v8c0 5.5 4.7 10.7 11 12 6.3-1.3 11-6.5 11-12V4L11 0z" fill="var(--accent)" fillOpacity="0.9"/>
              <path d="M7 11.5l2.5 2.5L15 8" stroke="white" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round"/>
            </svg>
            <span style={{ fontSize: '15px', fontWeight: 700, color: 'var(--text-primary)', letterSpacing: '-0.01em' }}>
              SF Capital
            </span>
          </Link>

          {/* Desktop links */}
          <div className="hide-mobile" style={{ display: 'flex', alignItems: 'center', gap: '4px' }}>
            {NAV_LINKS.map(link => {
              const isActive = pathname === link.href;
              return (
                <Link
                  key={link.href}
                  href={link.href}
                  style={{
                    fontSize: 'var(--text-sm)',
                    fontWeight: isActive ? 600 : 400,
                    color: isActive ? 'var(--text-primary)' : 'var(--text-secondary)',
                    textDecoration: 'none',
                    padding: '6px 12px',
                    borderRadius: 'var(--radius-md)',
                    background: isActive ? 'rgba(255,255,255,0.06)' : 'transparent',
                    transition: 'background var(--transition), color var(--transition)',
                  }}
                >
                  {link.label}
                </Link>
              );
            })}
          </div>

          {/* Spacer */}
          <div style={{ flex: 1 }} />

          {/* Live indicator */}
          <div style={{ display: 'flex', alignItems: 'center', gap: '6px' }}>
            <div className="live-dot" />
            <span style={{ fontSize: 'var(--text-xs)', fontWeight: 600, color: 'var(--gain)', letterSpacing: '0.08em' }}>
              LIVE
            </span>
          </div>

          {/* Mobile menu toggle */}
          <button
            className="show-mobile"
            onClick={() => setMobileOpen(o => !o)}
            style={{ background: 'none', border: 'none', color: 'var(--text-primary)', cursor: 'pointer', padding: '4px' }}
            aria-label="Toggle menu"
          >
            {mobileOpen ? <X size={18} /> : <Menu size={18} />}
          </button>
        </div>
      </nav>

      {/* Mobile menu */}
      {mobileOpen && (
        <div style={{
          position: 'fixed',
          top: '56px', left: 0, right: 0,
          zIndex: 49,
          background: 'var(--bg-subtle)',
          borderBottom: '1px solid var(--border)',
          padding: 'var(--space-4)',
          display: 'flex',
          flexDirection: 'column',
          gap: 'var(--space-1)',
        }}>
          {NAV_LINKS.map(link => {
            const isActive = pathname === link.href;
            return (
              <Link
                key={link.href}
                href={link.href}
                onClick={() => setMobileOpen(false)}
                style={{
                  padding: '10px 12px',
                  borderRadius: 'var(--radius-md)',
                  fontSize: 'var(--text-base)',
                  fontWeight: isActive ? 600 : 400,
                  color: isActive ? 'var(--text-primary)' : 'var(--text-secondary)',
                  textDecoration: 'none',
                  background: isActive ? 'rgba(255,255,255,0.06)' : 'transparent',
                  borderLeft: isActive ? '2px solid var(--accent)' : '2px solid transparent',
                }}
              >
                {link.label}
              </Link>
            );
          })}
        </div>
      )}
    </>
  );
}
